import Axios from "axios";

let c = {
    ok() {
        alert(123)
    },
    ajax: () => {
        Axios.post('//localhost:3000/user/userLogin', {
            p : '{"firstName": "Fred","lastName": "Flintstone"}'
        })
            .then(function (response) {
                console.log(response);
            })
            .catch(function (error) {
                console.log(error);
            });
    }
};

export default c;


